import { useSelector, useDispatch } from 'react-redux'
import { removedCredentials } from '../features/authSlice'
import { LinkContainer } from 'react-router-bootstrap'
import { Nav, Navbar, Container, NavDropdown } from 'react-bootstrap'
import SearchBox from './SearchBox'
import { api } from '../features/api/apiSlice'

const Header = () => {

  const { userInfo } = useSelector(state => state.auth)
  const dispatch = useDispatch()

  const logoutHandler = () => {
    dispatch(removedCredentials())
    dispatch(api.util.invalidateTags(['User', 'Order']))
  }

  return (
    <header>
      <Navbar bg="primary" variant='dark' expand="lg" collapseOnSelect >
        <Container>
          <LinkContainer to={'/'}>
            <Navbar.Brand>eShop</Navbar.Brand>
          </LinkContainer>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <SearchBox />
            <Nav className="ms-auto">
              <LinkContainer to={'/cart'}>
                <Nav.Link><i className="fa-solid fa-cart-shopping"></i> Cart</Nav.Link>
              </LinkContainer>
              {userInfo ? (
                <NavDropdown title={userInfo.name} id='username'>
                  <LinkContainer to='/profile'>
                    <NavDropdown.Item>Profile</NavDropdown.Item>
                  </LinkContainer>
                  <NavDropdown.Item onClick={logoutHandler}>
                    Logout
                  </NavDropdown.Item>
                </NavDropdown>
              )
              :
              <LinkContainer to = { '/login' }>
                <Nav.Link><i className="fa-solid fa-user"></i> Sign In</Nav.Link>
            </LinkContainer>}
            {userInfo && userInfo.isAdmin && (
              <NavDropdown title='Admin' id='adminmenu'>
                <LinkContainer to='/admin/userlist'>
                  <NavDropdown.Item>Users</NavDropdown.Item>
                </LinkContainer>
                <LinkContainer to='/admin/productlist'>
                  <NavDropdown.Item>Products</NavDropdown.Item>
                </LinkContainer>
                <LinkContainer to='/admin/orderlist'>
                  <NavDropdown.Item>Orders</NavDropdown.Item>
                </LinkContainer>
              </NavDropdown>
            )}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  )
}

export default Header
